@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
:root {
  --brand-background-color: #14151a;
  --brand-foreground-color: #161b22;
  --brand-text-grey: #8a8f98;
  --brand-red: #e63022;
  --brand-magenta: #ee0979;
  --brand-orange: #ff6a00;
  --brand-magenta: #ee0979;
  --brand-orange: #ff6a00;
  --brand-main-gradient: linear-gradient(
    45deg,
    #e63022d9,
    #ee0979d9,
    #ff6a00d9
  );
}

body {
  margin: 0;
  font-family: "Lato", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.application_layout {
  width: 100vw;
  height: 100vh;
  max-width: 100vw;
  overflow-x: hidden;
}

.gitraffe-button {
  border-radius: 24px;
  font-weight: 600;
}

@media screen and (max-width: 500px) {
  .home_cards-container {
    width: 100%;
    max-width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }
}

@media screen and (min-width: 500px) {
  .home_cards-container {
    width: 100%;
    max-width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    padding: 0px 24px 24px 24px;
  }
}

.header_root {
  padding: 4px 24px;
  /*box-shadow: 0 2px 8px #f0f1f2;*/
  background-color: white;
}

.header_release-date {
  font-weight: 800;
  display: flex;
  height: 100%;
  align-items: center;
  font-size: 16px;
}

.buttons{
    margin: 20px 10px 20px 10px;
    display: block;
    text-align: center;
    font-weight: 500;
    padding: 12px 20px 12px 20px;
  }
a.flatbutt{
    background: #e26c3d;
    border-radius: 3px;
    color: #eee;
    font-size: 90%;
